<template>
	<li :class="{active: tabNav.active}" @click="setActive(tabNav)">
		<a data-toggle="tab">{{tabNav.header}}</a>
	</li>
</template>

<script>
	export default {
		name: 'TabNav',
		props: {
			tabNav: Object,
			require: true
		},
		methods: {
			setActive(tabNav){
				//this.$dispatch('activate', tabNav);// tab activate, nav 
				this.$dispatch('setActiveIndex', tabNav.index); //tabpane activate , activeIndextabpane.active
			}
		}
	}
</script>


