import Vue from 'vue';
import VueResource from 'vue-resource';
Vue.use(VueResource);

/**
 * @ resourceapi
 * user, special
 */
var VIP = require('services/public');
var resources = {};

/**
 * @url  
 * @params  resource User.get({userId:2})
 * @actions resource CURD and others
 * @options action http
 */
// Vue.resource(url, params, actions, options);


function setResource(name, otherActions, params){
	name = VIP.capitalize(name);
	params = params || {};

	var defActions = {
		get: {
			url: 'get'+name //urlapiKey
		},query: {
			url: 'query'+name
		},save: {
			url: 'save'+name,
			method: 'POST'
		},update: {
			url: 'update'+name,
			method: 'POST'
		},remove: {
			url: 'del'+name
		}
	};
	var actions = VIP.extend({}, defActions, otherActions);

	// /UserApi
	var resource = Vue.resource('/'+name+'Api', params, actions, {method: 'GET'});
	resources[name] =  resource;
}

var resourceNames = ['user', 'special'];
resourceNames.forEach(setResource);

module.exports = resources;


