<template>
<div class="demo-page aside-demo container">
	<h2 class="text-center">Aside component</h2>
	
	<!--  -->
	<h3></h3>
	<table class="table table-bordered">
	    <thead>
	        <tr>
	            <th></th>
	            <th></th>
	            <th></th>
	            <th></th>
	        </tr>
	    </thead>
	    <tbody>
	        <tr>
	            <td width="15%">show <span class="badge">2</span></td>
	            <td width="10%"><code>Boolean</code></td>
	            <td width="10%"><code>true</code></td>
	            <td></td>
	        </tr>
	        <tr>
	            <td>placement</td>
	            <td><code>String</code></td>
	            <td><code>right</code></td>
	            <td>
	                 
	                <code>left</code>
	                <code>right</code>
	            </td>
	        </tr>
	        <tr>
	        	<td>header</td>
	        	<td><code>String</code></td>
	        	<td><code>null</code></td>
	        	<td></td>
	        </tr>
	        <tr>
	        	<td>width</td>
	        	<td><code>Number</code></td>
	        	<td><code>320</code></td>
	        	<td></td>
	        </tr>
	    </tbody>
	</table>
	
	
	<!--  -->
	<h3>: </h3>
	<pre>{{ opt | json }}</pre>
	
	<!-- try start-->
	<div class="try">
		<vform horizontal="true" label-cols="4">
			<div class="row">
				<div class="col-md-4">
					<strong>placement:</strong>
					<radio-group :value.sync="opt.placement" type="primary">
						<radio-btn value="left">left</radio-btn>
						<radio-btn value="right">right</radio-btn>
					</radio-group>
				</div>
				<div class="col-md-4">
					<strong>show:</strong>
					<label><input type="checkbox" name="chk" id="chk" v-model="opt.show"> {{opt.show}}</label>
				</div>
				<div class="col-md-4">
					<form-group label="header:" :value.sync="opt.header"></form-group>
				</div>
			</div>
			<div class="row">
				<div class="col-md-4">
					<form-group label="width:" :value.sync="opt.width  | mknumber"></form-group>
				</div>
			</div>
		</vform>
	</div>
	<!-- try end -->


	<btn type="warning" size="1g" @click="opt.show = !opt.show">Aside</btn>
	
	<aside :header="opt.header" :show.sync="opt.show" :placement="opt.placement" :width="opt.width">
		<h2>dosomething,</h2>
		<p>
			json jsonp
		</p>
		<p>
			scriptjsjsurljsonjsonp jsonpjsscript
		</p>
		<btn>submit</btn>
	</aside>
	
</div>
</template>

<script>
	var options = {
		header: '',
		show: true,
		placement: 'right',
		width: 300
	};

	export default {
		data: function(){
			return {
				'opt': options
			};
		},
		filters:{
			mknumber:{
				read(val){
					return val;
				},
				write(val){
					return Number(val);
				}
			}
		}
		
	}

</script>




